.profile_container_my{
  width: 100%;
  height: 100vh;
  overflow: hidden;
  .profile_home{
    color:#ca0000;
    font-size: 40px;
    position: fixed;top: 20px;left: 20px;
    
  }

  .profile_commit{
    display:flex;
    flex-direction: column;
    justify-content:center;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    align-items: center;
    
    .profilebaseImg{
      background: url(../img/profileimgh.jfif)no-repeat 0 0;
      width:200px;
      height:200px;
      display:block;
      background-size:cover;
      border-radius: 10px;
      
      .profileImg_ok{
        color:#fff;
        width:200px;
        height:200px;
        display:block;
        background-size:cover;
        border-radius: 10px;
      }
    }

    .profile_name{
      color:#fff;
      margin-top: 40px;
      margin-bottom: 20px;
      font-size: 22px;
      
    }
    .profile_form_go{
      font-size: 20px;
      color:#fff;
      width: 50px;
      height: 50px;
      display: inline-block;
      line-height: 50px;
      box-sizing: border-box;
      text-align: center;
      border-radius: 10px;
      background: transparent;
      transition: all 0.2s linear 0s;
      &:hover{
        
        display: inline-block;
        width: 50px;
        height: 50px;
        border: 1px solid #ca0000;
        border-radius: 50%;
        line-height: 50px;
        box-sizing: border-box;
        background: transparent;
        text-align: center;
      }
    }
  }
}